* {
  box-sizing: border-box;
}

@font-face {
  font-family: mainFont;
  src: url();
}

.m-350 {
  max-width: 350px;
}

.Login-Box {
  border-color: black;
  border-width: 1px;
  border-style: solid;
  border-radius: 2px;
  background-color: white;
  line-height: 18px;
  margin-top: 10px;
  /* max-width: 350px; */
  height: 22em;
  position: relative;
  vertical-align: baseline;
  padding: 60px 0;
  top: 100px;
  left: 50%;
}

.Bottom-Box {
  max-width: 350px;
  line-height: 1;
  text-align: center;
  max-height: 1px;
}

.app-name {
  margin: 0;
  text-align: center;
  font-family: 'Dancing Script', cursive;;
}

.login-input {
  margin-left: 10%;
  margin-top: 5%;
  margin-right: 10%;
  width: 80%;
  background-color: whitesmoke;
  height: 35px;
  padding-left: 10px;
}
.no-border{
  border-style: none;
}
.thin-border{
  border-width: thin;
}
.button {
  position: relative;
  margin-left:10%;
  width: 80%;
  margin-top: 5%;
  background-color: rgba(0, 149, 256, 1);
  height: 30px;
  color: white;
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
  border-style: none;
  border-radius: 20px;
  cursor: pointer;
}

@media all and (max-width: 600px) {
  .Login-Box {
    position: relative;
    left: 8%;
  }
}
.form-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    border-color: black;
    border-width: 1px;
    border-style: solid;
    border-radius: 2px;
    background-color: white;
    line-height: 18px;
    margin-top: 10px;
    position: relative;
    left: 40%;
    vertical-align: center;
    padding: 60px 0;
}
hr {
    display: block;
    height: 1px;
    border: 0;
    border-top: 1px solid #000;
    margin: 1em 0;
    padding: 0;
}
